
.header {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 15vh;
}

.header>.logo {
  width: auto;
  height: 100%;
  pointer-events: none;
}

.home-page-content {
  display: flex;
  flex-direction: column;

  overflow-x: hidden;
  overflow-y: hidden;
  padding-bottom: 5vh;

  min-height: calc(85vh - 112px);
  height: calc(85vh - 112px);
  max-height: calc(85vh - 112px);
  background-color: transparent;
}

.home-background-video {
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  user-select: none;
  background-color: black;
}

.home-background-video>video {
  opacity: 50%;
  filter: blur(15px);
  width: auto;
  height: 100vh;
}

.home-page-highlight {
  display: flex;
  flex-direction: row;
  cursor: pointer;
  box-sizing: border-box !important;
  overflow: hidden;
  text-decoration: none;

  min-height: 14vh;
  max-height: 14vh;
  width: 90vw;
  max-width: 600px;
  margin: auto auto;
  padding: 2vh 2vw;

  border-radius: 8px;
  border: 1px solid rgb(34, 34, 34);
  background-color: #242830be;
}

.home-page-highlight>img, .home-page-highlight>i {
  width: auto;
  height: 100%;
  padding: 5%;
  margin: auto 2vw auto 0;
}

.home-page-highlight>div>h3 {
  font-size: 16px;
  font-family: metro-thin;
  color: rgb(224, 224, 224);
  margin: unset;
  padding: unset;
}

.home-page-highlight>div>p {
  font-size: 14px;
  font-family: metro-thin;
  color: rgb(224, 224, 224);
  margin: 1vh 0;

  max-width: 100%;
  max-height: 8vh;

  padding: unset;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media all and (max-width: 720px){
  .home-page-highlight>div>p {
    font-size: 12px;
  }
}

@media all and (max-width: 640px){
  .home-page-highlight>div>p {
    font-size: 10px;
  }
}

@media all and (max-width: 320px){
  .home-page-highlight>div>p {
    font-size: 8px;
  }
}

.social-footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  min-width: 300px;
  width: 25vw;
  height: 32px;
  max-height: 32px;

  margin: 0 auto 0 auto;
  padding: 24px;
  filter: drop-shadow(0 1px 5px rgba(210, 210, 255, 0.66));
}

.social-footer>a {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.social-footer>a>img {
  width: 24px;
}
