@import url('https://maxcdn.bootstrapcdn.com/font-awesome/4.2.0/css/font-awesome.min.css');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.2/css/all.min.css');
@font-face {
  font-family: "metro-bold";
  src: url("assets/fonts/Metropolis-Bold.otf");
}
@font-face {
  font-family: "metro-medium";
  src: url("assets/fonts/Metropolis-Medium.otf");
}
@font-face {
  font-family: "metro";
  src: url("assets/fonts/Metropolis-Regular.otf");
}
@font-face {
  font-family: "metro-thin";
  src: url("assets/fonts/Metropolis-Thin.otf");
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'metro';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

header {
  z-index: 999;
  display: flex;
  position: fixed;
  text-align: center;
  flex-direction: row;
  align-items: left;
  justify-content: left;
  height: 10vh;
  min-width: 100vw;
  min-height: 48px;
  max-height: 48px;
  pointer-events: all;
  background-color: #282c34;
}

header>#nav-left {
  display: flex;
  text-align: center;
  flex-direction: row;
  align-items: left;
  justify-content: left;
  width: 100%;
  min-height: 48px;
  max-height: 48px;
  pointer-events: all;
}

header>#nav-left>a {
  width: 100%;
  height: 48px;
  padding: 14px 0 0 0;

  font-family: metro;
  font-size: 18px;

  color: white;
  text-decoration: none;
  pointer-events: all;
  user-select: none;
}

header>#nav-left>a:hover {
  background-color: #3a3d44;
}

header>#nav-right {
  display: flex;
  justify-content: right;
  align-items: right;
  margin: 0 5vw 0 auto;
  padding-left: 8px;
}

header>#nav-right>button {
  font-family: metro;
  font-size: 16px;
  text-transform: uppercase;

  min-width: 132px;
  width: 100%;
  max-width: 200px;
  height: calc(100% - 16px);
  margin: auto 25px auto auto;
  cursor: pointer;
  border: unset;
  border-radius: 6px;
  color: white;
  background-color: transparent;
}

header>#nav-right>button:hover {
  background-color: rgb(180, 107, 48) !important;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'metro-medium';
}

p {
  color: white;
  font-family: metro;
  width: 100%;
}

img {
  user-select: none;
  pointer-events: none;
}

#container {
  min-height: calc(100vh - 48px);
  max-height: calc(100vh - 48px);
  overflow-y: auto;
  background-color: #282c34;
}

.no-select {
  pointer-events: none;
  -khtml-user-select: none;
  -o-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.content {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 0 0 128px 0;
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
}

.page-content {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: left;
  height: 100vh;
  color: rgb(40, 44, 52);
  background-color: #e5e7eb;
  font-size: calc(10px + 2vmin);
}

.error-page-content {
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100vw;
  height: 100vh;
  margin-top: -28px;

  color: rgb(40, 44, 52);
  background-color: #e5e7eb;
  font-size: calc(10px + 2vmin);
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: rgb(212, 96, 0);
  outline: 1px solid rgb(77, 77, 77);
}

.grow {
  transition: all .2s ease-in-out;
}

.grow:hover {
  transform: scale(1.066);
}

.flex-between {
  display: flex;
  justify-content: space-between;
}

.flex-left {
  display: flex;
  align-items: left;
  align-content: left;
  justify-content: left;
  min-width: 120px;
}

.flex-center {
  display: flex;
  justify-content: center;
  min-width: 120px;
}

.flex-right {
  display: flex;
  justify-content: right;
  min-width: 120px;
}
