
hr {
  margin: 8px 0 8px 0;
}

th {
  border-bottom: 1px solid #2b2b2b;
}

td {
  padding: 8px;
  user-select: none;
  border-bottom: 1px solid #4b4b4b;
}

.selectedRow {
  background-color: #bebebe;
}

select {
  width: 300px;
  height: 28px;
  margin: 0 16px 16px 16px;
  font-size: 18px;
  outline: none;
  border-radius: 3px;
  border: 0 solid transparent;
  background-color: #f0f0f0;
}

button {
  width: 142px;
  height: 28px;
  margin: 0 8px;
}

#Panel-Header {
  display: flex;
  justify-content: left;
  width: 100%;
  height: 48px;
  font-size: 16px;
  background-color: #282c34;
}

#Panel-Header>p {
  margin: auto auto;
  text-transform: uppercase;
}

#Panel-Tab-Header {
  display: flex;
  text-align: left;
  align-items: center;
  justify-content: left;

  width: calc(100% + 10vw);
  height: 50px;
  margin: 0 0 0 -5vw;
  padding: 0 calc(5vw + 8px);

  overflow: hidden;
  box-shadow: 1px 0px 8px #282c34;
}

#Panel-Body {
  display: flex;
  width: 100%;
  min-height: calc(100vh - 48px);
  overflow-x: hidden;
  overflow-y: hidden;
}

#Panel-Menu-Button {
  display: none;
  position: fixed;
  z-index: 999;
  bottom: 16px;
  left: 16px;
  width: 48px;
  height: 48px;
  cursor: pointer;
  pointer-events: all;
  background-color: #282c34;
  border-radius: 50%;
}

#Panel-Menu-Button>i {
  color: white;
  font-size: 28px;
  width: 100%;
  height: 100%;
  margin: 10px auto;
  text-align: center;
}

#Panel-Menu {
  display: none;
  flex-direction: column;
  min-width: 100%;
  min-height: 100%;
  background-color: #282c34;
}

#Panel-Menu-Container {
  z-index: 2;
  min-width: 250px;
  max-width: 250px;
  background-color: #282c34;
  box-shadow: 1px 0px 8px #282c34;
}

#Panel-Menu-Container:hover>#Panel-Menu, #Panel-Menu:hover {
  display: flex !important;
  flex-direction: column !important;
}

#Panel-Menu-Icons {
  display: flex;
  height: 48px;
  overflow-y: hidden;
}

.Panel-Menu-Icon {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 48px;
  cursor: pointer;
}

.Panel-Menu-Icon:hover {
  background-color: #363b46;
}

.Panel-Menu-Icon>img {
  width: 32px;
  height: 32px;
  margin: auto 0;
}

.Panel-Menu-Header {
  display: flex;
  font-size: 24px;
  user-select: none;
  pointer-events: none;
  color: rgb(198, 201, 207);
  text-align: left;
  padding: 48px 0 16px 8px;
  border-bottom: 1px solid rgb(192, 192, 192);
}

.Panel-Menu-Header>.fa-solid {
  width: 100%;
  text-align: right;
  padding-right: 16px;
}

.Panel-Menu-Option {
  display: flex;
  cursor: pointer;
  user-select: none;
  color: rgb(198, 201, 207);
  text-align: left;
  padding: 16px 0 16px 8px;
  border-bottom: 1px solid rgb(192, 192, 192);
}

.Panel-Menu-Option>p{
  font-weight: normal;
  font-family: metro;
  font-style: normal;
  color: rgb(198, 201, 207);
}

.Panel-Menu-Option>.fa-solid {
  display: none;
  font-size: 64px;
  padding-bottom: 16px;
}

.Panel-Menu-Option:hover, #Panel-Menu-Close:hover {
  background-color: #363b46;
}

#Panel-Menu-Close {
  display: none;
}

#panel-users {
  display: flex;
  flex-wrap: wrap;
  align-items: left;
  justify-content: left;
  width: 100%;
  margin-top: 32px;
}

.panel-users-user {
  display: flex;
  cursor: pointer;
  height: 48px;
  min-width: calc(300px - 5vw);
  width: 48%;
  border-radius: 5px;
  padding: 8px 18px 8px 18px;
  margin: 15px auto 0 2%;
  background-color: rgb(231, 227, 222);
}

.panel-users-user:hover {
  box-shadow: 0px 0px 4px #b4b4b4;
}

.panel-users-user>p {
  color: black;
  width: 100%;
  height: 32px;
  padding: 6px 0 0 0;
  margin: auto 0 auto 0;
  user-select: none;
}

.panel-users-user>img {
  width: 32px;
  height: 32px;
  margin: auto 18px auto 0;
  user-select: none;
}

@media screen and (max-width: 800px) {
  #panel-users {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 48px auto;
    width: fit-content;
  }
  .panel-users-user {
    min-width: unset;
    width: 90vw;
    margin: 8px 0;
  }
}

.text-banner {
  display: flex;
  justify-content: left;
  height: 48px;
  max-width: 90%;
  border-radius: 5px;
  padding: 8px 18px;
  margin: 16px 0;
  background-color: rgb(231, 227, 222);
}

.text-banner>b {
  user-select: none;
}

.text-banner>p {
  cursor: pointer;
  user-select: all;
}

.text-banner>p, .text-banner>b {
  color: #161920;
  margin: auto 8px;
}

.markdown-container {
  overflow: auto;
  width: calc(50% - 64px);
  background-color: rgb(231, 227, 222);
}

.markdown-container>h1,
.markdown-container>h2 {
  text-align: center;
  margin: unset;
  padding: 18px;
  color:rgb(32, 32, 32);
}

.markdown-container>h3,
.markdown-container>h4,
.markdown-container>h5,
.markdown-container>h6 {
  text-align: left;
  margin: unset;
  padding: 18px;
  color:rgb(32, 32, 32);
}

.markdown-container>p {
  margin: unset;
  padding: 18px;
  color:rgb(32, 32, 32);
}

.Panel-Option-Header {
  color: rgb(32, 32, 34);
  text-align: center;
  margin: 48px auto;
  letter-spacing: 2px;
}

.Panel-Option-Header>img {
  width: 16px;
  height: 16px;
  margin-left: 8px;
}

.Panel-Options-Container {
  display: flex;
  flex-direction: column;
  justify-content: left;
  max-width: 720px;
  min-height: 500px;
  margin: 5vh auto;
  border-radius: 3px;
  border: 1px solid #363b46;
  background-color: #f1ffff;
  padding: 5%;
}

.Panel-Options-Container>h3 {
  margin: 0 0 32px 0;
}

#Panel-Content {
  width: 100%;
  height: calc(100vh - 48px);
  overflow-x: hidden;
  overflow-y: auto;
  padding: 0 5%;
}

#Panel-Content-Left, #Panel-Content-Right {
  width: 50%;
}

#Panel-Content-Container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5vh auto;
  min-height: calc(90vh - 100px);
  border-radius: 3px;
  border: 1px solid black;
  background-color: #f1ffff;
}

#E-Panel-Login {
  display: flex;
  flex-direction: row;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100vw;
  height: calc(120vh - 48px);
}

#E-Panel-Login-Left {
  display: flex;
  user-select: none;
  pointer-events: none;

  flex-direction: column;
  justify-content: center;
  align-items: center;

  min-width: 50vw;
  max-width: 50vw;
  height: 100%;

  margin-top: -10%;
  margin-bottom: -5%;
  background-color: #242830;
}

#E-Panel-Login-Left>img {
  margin-bottom: 5%;
}

#E-Panel-Login-Right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  min-width: 50vw;
  max-width: 50vw;
  height: 100%;

  margin-top: -7.5%;
  margin-bottom: -2.5%;
}

#E-Panel-Login-Right>h1 {
  margin: 5% 0;
  padding-top: 5%;
}

#E-Panel-Login-Right>div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
  margin-top: 5%;
}

#E-Panel-Login-Right>div>button {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  min-width: 148px;
  padding: 16px;

  color: rgb(168, 114, 64);
  border-radius: 6px;
  border: 2px solid rgb(168, 114, 64);
  background-color: #f0f0f0;
}

#E-Panel-Login-Right>div>button:hover {
  color: #f0f0f0;
  background-color: rgb(168, 114, 64);
}

#E-Panel-Login-Right>input {
  z-index: 1;

  width: 50%;
  max-height: 64px;
  min-height: 64px;
  margin: 0 auto 16px auto;
  padding: 16px 16px 0 16px;
  border: 0 solid #fff;
  inset: none;

  border-radius: 6px;
  -webkit-border-radius: 6px;
}

#E-Panel-Login-Right>p {
  z-index: 2;
  user-select: none;
  pointer-events: none;

  width: 50%;
  height: 32px;
  margin: 16px 0 -36px 0;
  padding: 0 8px 0 8px;

  color:rgb(85, 85, 85);
  border-radius: 6px;
}

#E-Panel-Login-Right>p>img {
  user-select: none;
  pointer-events: none;
  margin: 4px 12px -2px 8px;
}

#E-Panel-Login-Right>a {
  margin: 5% 0 -10% 0;
  padding-bottom: 10%;
}

.login-error {
  text-align: center;
  padding: 8px 0 0 0 !important;
  margin: 0 auto 2% auto !important;
  color: white !important;
  background-color: red;
}

@media screen and (max-width: 1250px) {
  #E-Panel-Login {flex-direction: column !important;}
  #E-Panel-Login-Left {display: none !important;}
  #E-Panel-Login-Right {min-width: 100vw !important; margin-top: -15vh;}
  #Panel-Content-Container {flex-direction: column;}
  #Panel-Content-Right, #Panel-Content-Left {margin: 32px 0; width: 100%;}
  #Panel-Menu-Container {
    min-width:100vw;
    width:100vw;
    max-width:100vw;
  }
  #Panel-Menu-Options {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction:row;
    flex-wrap:wrap;
    min-height: calc(100vh - 150px) !important;
  }
  #Panel-Menu-Close {
    display: flex;
    position: fixed;
    justify-content: center;
    cursor: pointer;
    bottom: 0;
    width: 100vw;
    height: 50px;
    padding: 6px;
    color: white;
    font-size: 38px;
    text-align: center;
    border-top: 1px solid #3d3d3d;
    background-color: #282c34;
  }
  .Panel-Menu-Option {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-width:25vw;
    max-width:25vw;
    min-height:25vh;
    max-height:25vh;
    border:unset;
    border-radius: 3px;
  }
  .Panel-Menu-Option>.fa-solid {display: flex;}
  .Panel-Menu-Header {display:none;}
}

@media screen and (max-width: 620px) {
  #E-Panel-Login-Right>h1 { padding-top: unset; }
  #E-Panel-Login-Right>div { flex-direction: column; }
  #E-Panel-Login-Right>div>button { margin-bottom: 32px; }
  .ep-dropdown {
    min-width: 100%;
    margin-left: -5% !important;
    margin-right: unset !important;
  }
}

/* PANEL SELECT VIEW */

.ep-panel-btn {
  width: 100%;
  max-width: 128px;
  height: 32px;
  margin: 64px 0 0 0;
  padding: 0 16px;
  font-size: large;
}

.ep-bottom-btn {
  color: #282c34;
  cursor: pointer;
  margin: 0 auto 0 auto;
  max-width: fit-content;
  cursor: pointer;
  border-bottom: 2px solid transparent;
}

.ep-bottom-btn:hover {
  border-bottom: 2px solid #242830;
}

.ep-login-panel-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start !important;

  min-height: 25vh;
  max-height: 50vh;
  overflow-y: auto;

  border-radius: 5px;
  border: 2px solid rgb(160, 160, 160);
}

.ep-login-panel-btn {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  width: 85%;
  background-color: #282c34;
  border-radius: 3px;
  padding: 16px;
  margin: 16px 0;
}

.ep-login-panel-btn:hover {
  background-color: #363b46;
}

.ep-login-panel-btn>img {
  user-select: none;
  min-width: 48px;
  min-height: 48px;
  max-width: 48px;
  max-height: 48px;
  margin: auto 0 auto 16px;
}

.ep-login-panel-btn>p {
  user-select: none;
  margin: auto 0 auto 32px;
}

.full-modal-options>button {
  color: #363b46;
  width: 198px;
  height: 36px;
  font-size: 18px;
  border-radius: 3px;
  background-color: #ececec;
}

.ep-create-record-view {
  width: 95%;
  justify-content: space-between;
  margin: 0 auto 64px auto;
}

.ep-create-record-view>div>p {
  color: black;
  width: 100%;
  margin: 16px 0;
  font-weight: 600;
}

.ep-create-record-view>input {
  width: 50%;
}

.ep-ui-toolbar {
  display: flex;
  justify-content: left;
  align-items: center;
  text-align: left;
  margin: 0 -5vw 0 -5vw;
  height: 48px;
  background-color: #282c34;
  box-shadow: 1px 0px 8px #282c34;
}

.ep-ui-toolbar>select {
  cursor: pointer;
  color: white;
  border: unset;
  background-color: #282c34;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);;
  -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
  -webkit-padding-end: 20px;
  -webkit-padding-start: 2px;
  user-select: none;
  -webkit-user-select: none;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ep-ui-toolbar>select:hover {
  background-color: #31353d;
}

.ep-ui-toolbar>select:focus {
  outline: none;
}

.ep-ui-header {
  font-family: metro;
  color: #f0f0f0;
  margin: 0 5vw 0 auto;
  text-align: right;
}

.ep-btn, .ep-btn-hover {
  color: white;
  border: unset;
  inset: unset;
  background-color: unset;
  cursor: pointer;
  font-family: 'metro';
  font-size: small;
  font-weight: 400;
  vertical-align: middle;
}

.ep-btn-hover:hover, .ep-dropdown>.ep-btn:hover {
  color: black;
  background-color: #cacaca;
}

.ep-dropdown>.ep-btn {
  user-select: none;
  justify-content: center;
  align-items: center;
  text-align: left;
  padding: 12px;
  margin: 0 -12px;
}

.ep-btn-orange {
  border: unset;
  inset: unset;
  cursor: pointer;
  color: #f0f0f0 !important;
  border: 1px solid black;
  border-radius: 3px;
  background-color: rgb(168, 114, 64) !important;
  font-family: 'metro';
  font-size: small;
  font-weight: 400;
  vertical-align: middle;
}

.ep-btn-green {
  border: unset;
  inset: unset;
  cursor: pointer;
  color: white;
  border: 1px solid black;
  border-radius: 3px;
  background-color: rgb(105, 180, 105);
  font-family: 'metro';
  font-size: small;
  font-weight: 400;
  vertical-align: middle;
}

.ep-btn-red {
  border: unset;
  inset: unset;
  cursor: pointer;
  color: white;
  border: 1px solid black;
  border-radius: 3px;
  background-color: rgb(180, 105, 105);
  font-family: 'metro';
  font-size: small;
  font-weight: 400;
  vertical-align: middle;
}

.ep-btn-red:disabled, .ep-btn-green:disabled {
  cursor: not-allowed;
  opacity: .33;
  color: black;
  background-color: rgb(172, 172, 172);
  vertical-align: middle;
}

.ep-btn-selected {
  border: unset;
  inset: unset;
  cursor: pointer;
  background-color: #cacaca;
  font-family: 'metro';
  font-size: small;
  font-weight: 400;
  vertical-align: middle;
}

.ep-dropdown {
  z-index: 99;
  position: absolute;
  width: fit-content;
  padding: 16px;
  color: #f0f0f0;
  background-color: #282c34;
  border: 1px solid #6b6b6b;
  border-top: unset;
  border-radius: 5px;
  border-top-right-radius: unset;
  border-top-left-radius: unset;
  font-family: 'metro';
  font-size: small;
  font-weight: 400;
}

.ep-dropdown>p {
  color: black;
  width: 100%;
  margin: unset;
}

.ep-input {
  font-size: medium;
  border: unset;
  inset: unset;
  color: black;
  height: 25px;
  border: 1px solid rgb(90, 90, 90);
  border-radius: 3px;
  background-color: rgb(248, 247, 247);
}

.ep-setting-container {
  height: 98px;
}

.ep-setting-container>div {
  display: flex;
  justify-content: space-between;
  margin: 0 2px 0 0;
}

.ep-setting-container>div>p {
  color: black;
  user-select: all;
  cursor: pointer;
  margin: unset;
}

.ep-setting-label {
  display: flex;
  justify-content: space-between;
  font-weight: 600;
}

.ep-setting-form>p {
  margin: unset;
  padding: unset;
  color: black;
}

.ep-setting-form>p>select {
  margin: 0 0;
  padding: 2px;
  border: unset;
  border-radius: 6px;
  background-color: rgba(255,255,255,0.5);
}

.ep-setting-form>p>input {
  margin: 0 0;
  padding: 4px;
  border: unset;
  border-radius: 6px;
  background-color: rgba(255,255,255,0.5);
}

.ep-code-editor, .ep-code-editor-small, .ep-web-console, .ep-web-console-small {
  margin: 0 -5vw 0 -5vw;
}

.ep-code-editor>.cm-theme>.cm-editor {
  min-height: calc(100vh - 96px) !important;
  height: calc(100vh - 96px) !important;
  max-height: calc(100vh - 96px) !important;
}

.ep-code-editor-small>.cm-theme>.cm-editor {
  min-height: calc(65vh - 96px) !important;
  height: calc(65vh - 96px) !important;
  max-height: calc(65vh - 96px) !important;
}

.ep-web-console>*>*>.index_editor__JoDSg {
  white-space: pre;
  word-wrap: break-word;
}

.ep-web-console-small>*>*>.index_editor__JoDSg {
  white-space: pre;
  word-wrap: break-word;
  border-radius: unset !important;
  padding: 16px !important;
  min-height: 35vh !important;
  height: 35vh !important;
  max-height: 35vh !important;
  overflow-y: auto;
}

.ep-web-console-small>*>*>*>.index_lineText__j6qNj {
  display: none !important;
}

.ep-web-console>*>*>.index_editor__JoDSg {
  border-radius: unset !important;
  padding: 16px !important;
  min-height: calc(100vh - 96px) !important;
  height: calc(100vh - 96px) !important;
  max-height: calc(100vh - 96px) !important;
}

.ep-window-fader {
  z-index: 998;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100vw;
  min-height: 100vh;
  max-width: 100vw;
  max-height: 100vh;
  background-color: #161920;
  opacity: 90%;
}

.ep-window {
  z-index: 999;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  min-width: 300px;
  min-height: 300px;
  width: 80vw;
  height: 80vh;

  border-radius: 3px;
  background-color: #242830;
}

.ep-window>.ep-window-title {
  user-select: none;
  margin: 8px 0 0 8px;
  color: white;
  font-size: 18px;
  font-family: "metro-thin";
}

.ep-window-close {
  display: flex;
  position: absolute;
  top: 4px;
  right: 12px;
  font-size: 22px;
  color: white;
  cursor: pointer;
}

.ep-file-browser {
  display: flex;
  position: absolute;
  top: 30px;
  left: 0;
  overflow: auto;

  min-width: 300px;
  min-height: 300px;
  width: 100%;
  height: calc(100% - 55px);
}

.ep-file-browser>ul {
  padding: 16px;
  min-width: 100%;
}

@media screen and (max-width: 1250px) {
  #E-Panel-Login { flex-direction: column !important; }
  #E-Panel-Login-Left { display: none !important; }
  #E-Panel-Login-Right { min-width: 100vw !important; margin-top: -15vh; }
  #Panel-Content-Container { flex-direction: column; }
  #Panel-Content-Right, #Panel-Content-Left { margin: 32px 0; width: 100%; }
}

@media screen and (max-width: 620px) {
  #E-Panel-Login-Right>h1 { padding-top: unset; }
  #E-Panel-Login-Right>div { flex-direction: column; }
  #E-Panel-Login-Right>div>button { margin-bottom: 32px; }
}
